*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
  margin: 0;
}

p {
  text-wrap: pretty;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}

body {
  display: flex;
  justify-content: center;
  background-color: #111315;
}

.container {
  width: 1400px;
  margin: 0 auto;
}

.d-flex {
  display: flex;
}

.justify-content-between {
  justify-content: space-between;
}

.justify-content-center {
  justify-content: center;
}

.align-items-center {
  align-items: center;
}

a {
  cursor: pointer;
}

.link {
  color: #FFF;
}
.link:hover {
  color: #499BFC;
}

.underline {
  text-decoration: underline;
}

.py-10 {
  padding: 10px 0;
}

.flex-column {
  flex-direction: column;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: #499BFC;
}

.text-white {
  color: #FFF;
}

.pr-5 {
  padding-right: 5px;
}

.pt-10 {
  padding-top: 10px;
}

.header, .footer {
  background-color: #1A1D1F;
  padding: 30px 0;
  font-size: 18px;
  color: #FFF;
}

.pagination {
  margin: 20px 0;
}
.pagination .page {
  padding: 5px 8px;
  background-color: #1A1D1F;
  font-size: 20px;
  font-weight: 600;
  margin: 0 5px;
  border-radius: 13px;
}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 33%);
  column-gap: 6px;
  row-gap: 6px;
  place-items: center;
  padding: 33px 0;
  border-radius: 10px;
  background-color: #1A1D1F;
}

.card {
  background-color: #111315;
  padding: 20px;
  border-radius: 8px;
  color: #FFF;
  width: 400px;
}
.card img {
  width: 360px;
  height: 180px;
}
.card .description {
  height: 70px;
}

.category {
  color: #FFF;
}

.main {
  margin: 65px 0;
}
.main .header {
  background-color: #111315;
  padding: 20px 33px;
}